const emo = Object.keys(emoticons);

export default function App() {
  const [emoji, setEmoji] = useState("");
  const [meaning, setMeaning] = useState(
    "Click on the emoji to find out it's meaning"
  );

  function changeHandler(event) {
    const input = event.target.value;
    setEmoji(input);
    if (input in emoticons) {
      setMeaning(emoticons[input]);
    } else {
      setMeaning("Oops!! We don't recognise this emoji ğŸ˜¢ ");
    }
  }

  function emoClickHandler(input) {
    setMeaning(emoticons[input]);
  }

  return (
    <>
      <div className="App">
        <h1>Emojipedia</h1>
        <h3>~~Communicate without embarassing yourself~~</h3>
        <p>
          It's hard to believe but there was a time before emojis when humans
          were forced to communicate through words alone.Communications were
          fraught and confusing. It was a dark time.
        </p>
        <h4>BUT NOT ANYMORE!!!</h4>
        <p>
          Here is a emoji dictionary which can help you find the actual meaning
          of the emojis you want to use.
        </p>
        <input
          onChange={changeHandler}
          className="inputbox"
          placeholder="Search your emoji"
        />
        <h2>{emoji}</h2>
        <h3>{meaning}</h3>
        {emo.map(function (emoji) {
          return (
            <span onClick={() => emoClickHandler(emoji)} key={emoji}>
              {" "}
              {emoji}
            </span>
          );
        })}
      </div>
    </>
  );
}
